    name: 'slide-list',
    computed: {
        Screen_width() {
            return uni.getSystemInfoSync().windowWidth;
        }
    },
    data() {
        return {
            start_slide_x: 0,
            btnWidth: 0,
            startX: 0,
            LastX: 0,
            startTime: 0,
            NowMessageList: [
                {
                    img: '../../static/1.jpg',
                    name: '狐狸叔叔',
                    message: '今晚去吃饭吗?',
                    time: '19:08',
                    top: 0,
                    slide_x: 0,
					id:1
                },
                {
                    img: '../../static/2.jpg',
                    name: '老虎爸爸',
                    message: '黑发不知勤学早，白首方悔读书迟。 —— 颜真卿《劝学诗》',
                    time: '02:08',
                    top: 0,
                    slide_x: 0,
					id:2
                },
                {
                    img: '../../static/3.jpg',
                    name: '偶遇妹子',
                    message: '忽如一夜春风来，千树万树梨花开。 —— 岑参《白雪歌送武判官归京》',
                    time: '02:08',
                    top: 0,
                    slide_x: 0,
					id:3
                },
                {
                    img: '../../static/4.jpg',
                    name: '男神',
                    message: '寂寞空庭春欲晚，梨花满地不开门。 —— 刘方平《春怨》',
                    time: '23:08',
                    top: 0,
                    slide_x: 0,
					id:4
                },
                {
                    img: '../../static/5.jpg',
                    name: '妹妹',
                    message:
                        '人生若只如初见，何事秋风悲画扇。 —— 纳兰性德《木兰词·拟古决绝词柬友》',
                    time: '02:08',
                    top: 0,
                    slide_x: 0,
					id:5
                }
            ]
        };
    },
	onLoad() {
		this.getList();
	},
    methods: {
		jump_detail(id){
// 			console.log(id);
// 			return;
			uni.navigateTo({
				url:'../system/system_detail?id='+id
			})
		},
		getList(){
			var type=1;
			var token=(uni.getStorageSync("user_info")).token;
			console.log(token)
			uni.request({
				url:'http://bus.liebianzhe.com/api/user/person_msg',
				method: 'POST',
				data:{
					token:token,
					type:type
				},
				success: res => {
					console.log(res)
				}
			})
		},
        // 滑动开始
        touchStart(e, index) {
            //记录手指放上去的时间
            this.startTime = e.timeStamp;
            //记录滑块的初始位置
            this.start_slide_x = this.NowMessageList[index].slide_x;
            // 按钮宽度
            uni.createSelectorQuery()
                .selectAll('.group-btn')
                .boundingClientRect()
                .exec(res => {
                    if (res[0] != null) {
                        this.btnWidth = res[0][index].width * -1;
                    }
                });
            // 记录上一次开始时手指所处位置
            this.startX = e.touches[0].pageX;
            // 记录上一次手指位置
            this.lastX = this.startX;
            //初始化非当前滑动消息列的位置
            this.NowMessageList.forEach((item, eq) => {
                if (eq !== index) {
                    item.slide_x = 0;
                }
            });
        },
        // 滑动中
        touchMove(e, index) {
            const endX = e.touches[0].pageX;
            const distance = endX - this.lastX;
            // 预测滑块所处位置
            const duang = this.NowMessageList[index].slide_x + distance;
            // 如果在可行区域内
            if (duang <= 0 && duang >= this.btnWidth) {
                this.NowMessageList[index].slide_x = duang;
            }
            // 此处手指所处位置将成为下次手指移动时的上一次位置
            this.lastX = endX;
        },
        // 滑动结束
        touchEnd(e, index) {
            let distance = 10;
            const endTime = e.timeStamp;
            const x_end_distance = this.startX - this.lastX;
            if (Math.abs(endTime - this.startTime) > 200) {
                distance = this.btnWidth / -2;
            }
            // 判断手指最终位置与手指开始位置的位置差距
            if (x_end_distance > distance) {
                this.NowMessageList[index].slide_x = this.btnWidth;
            } else if (x_end_distance < distance * -1) {
                this.NowMessageList[index].slide_x = 0;
            } else {
                this.NowMessageList[index].slide_x = this.start_slide_x;
            }
        },
        // 点击回复原状
        recover(index) {
            this.NowMessageList[index].slide_x = 0;
        },
        // 置顶
        top(index) {
            // this.NowMessageList[index].top = this.NowMessageList[index].top ? 0 : 1;
			this.NowMessageList[index].slide_x = 0;
// 			this.recover(index);
//             this.NowMessageList.sort((a, b) => {
//                 return b.top - a.top;
//             });
        },
        // 删除
        removeM(index,id) {
			var type=1;
			var token=(uni.getStorageSync("user_info")).token;
            this.NowMessageList.splice(index, 1);
			uni.request({
				url:'',
				method:'POST',
				data:{
					type:type,
					token:token,
					id:id
				}
			})
        }
    }
};
